.overlay-container {
    position: relative;
    height: 50px;
}

.overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0);
    transition: background 0.5s ease;
}

.overlay-container:hover .overlay {
    display: block;
    background: rgba(0, 0, 0, .3);
}

.overlay-close {
    position: absolute;
    top: 0;
    left: 0;
    text-align: center;
    opacity: 0;
    transition: opacity .35s ease;
}

.overlay-container button {
    opacity: 0;
}

.overlay-container:hover button {
    opacity: 1;
}

.absolute-center {
    margin: auto;
    position: absolute;
    top: 0; left: 0; bottom: 0; right: 0;
}